body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  font-family: 'Inter', system-ui;
}

.wrapper{
  background: #fff;
  box-shadow: 0px 10px 20px rgba(0,0,0,0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header{
  border-bottom: 1px solid #eaeaea;

  h3,p{
    margin: 0;
  }

  img {
    margin-right: 15px;
  }
}

.content{
  h1{
    margin: 0;
  }
}

.cartEmpty {
  text-align: center;

  p {
    width: 280px;
    line-height: 24px;
  }

  .greenButton {
    width: 245px;
    margin-top: 20px;

    &:hover {
      img {
        transform: rotate(180deg) translateX(3px);
      }
    }

    img {
      position: relative;
      top: 1px;
      transform: rotate(180deg);
      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

.search-block {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;

  .clear {
    position: absolute;
    right: 0;
    width: 18px;
    height: 18px;
    top: 14px;
    right: 15px;
  }

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
    width: 200px;
  }
}

.cartTotalBlock {
  ul {
    display: block;
    margin-bottom: 40px !important;

    li {
      display: flex;
      align-items: flex-end;
      margin-bottom: 20px;

      div {
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #dfdfdf;
        position: relative;
        top: -4px;
        margin: 0 7px;
      }
    }
  }

  .greenButton {
    position: relative;

    &:disabled {
      animation: button-loading 0.6s ease-in-out infinite;
    }

    &:hover {
      img {
        transform: translateX(5px);
      }
    }

    img {
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

.button{
  background: #fff;
  border: 0;
  padding: 0;
  cursor: pointer;
}

.search{
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 0 15px;

  input{
    border: 0;
    padding: 10px;
    font-size: 16px;
    width: 200px;
  }
}

.cart-item{
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  overflow: hidden;
  padding: 20px;

  p{
    font-size: 16px;
    margin: 8px 0;
  }

  b{
    font-size: 14px;
  }

  btn-delete{
    opacity: 0.5;
    cursor: pointer;
    transition: all 0.15s ease-in-out;

    &:hover{
      opacity: 1;
    }
  }
}

.cart-total{
  li{
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;

    div{
      flex: 1;
      height: 1px;
      border: 1px dashed #f3f3f3;
      position: relative;
      top: -3px;
      margin: 0 5px;
    }
  }

  .greenButton{
    position: relative;

    &:hover{
      img{
        transform: translateX(5px);
      }
    }

    img{
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.2s ease-in-out;
    }
  }
}

.greenButton {
  width: 100%;
  height: 55px;
  background: #9dd558;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.1s ease-in-out;

  &:disabled {
    background-color: #bebebe !important;
    cursor: default;
  }

  &:hover {
    background: lighten(#9dd558, 5%);
  }

  &:active {
    background: darken(#9dd558, 5%);
  }
}

@keyframes button-loading {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}